<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10" @click="exportHandle">
        <Icon type="ios-open-outline" class="zd-common-fs18 zd-common-mr5 zd-common-fl"/>导出
      </Button>
      <div class="zd-common-fr">
        <Select v-model="warehouse" :clearable="true" placeholder="全部仓库" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10">
          <Option v-for="item in warehouseArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <div class="zd-cm-tree zd-common-w300 zd-common-fl zd-common-mr10">
          <treeselect v-model="type" :multiple="true" :options="treeOptions" :normalizer="normalizer" :limit="2" :limitText="limitHandle" noOptionsText="暂无数据" :clearable="false" placeholder="全部物品分类" @close="onSelect"/>
        </div>
        <Input v-model="name" :maxlength="50" placeholder="物品名称" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <!--<div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>-->
    </div>
    <!--详情-->
    <Modal v-model="inventoryModal" :mask-closable="false" :width="600">
      <h3 slot="header" class="zd-common-modal-header">
        <span>现存量 - {{ goodsName }}</span>
      </h3>
      <vue-scroll>
        <div class="zd-common-h350">
          <Table border :columns="detailCol" :data="detailData"></Table>
        </div>
      </vue-scroll>

      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">

      </div>
    </Modal>
  </div>
</template>
<script>
  import Treeselect from '@riophae/vue-treeselect'
  import '@riophae/vue-treeselect/dist/vue-treeselect.css'
  export default {
    components: {
      Treeselect
    },
    data() {
      return {
        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code'
          },
          {
            title: '物品类别',
            align: 'center',
            key: 'classify'
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',

          },
          {
            title: '现存量',
            align: 'center',
            key: 'standingStock',
            render: (h, params) => {
              return h('div', [
                h('span', {
                  class:'zd-common-primary-c zd-common-cp',
                  on:{
                    click:()=>{
                      this.showDetail(params.index);
                    }
                  }
                },params.row.standingStock),
              ]);
            }
          },
          {
            title: '金额',
            align: 'center',
            key: 'amount',
          },
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,
        selectLoading:false,
        //查询
        dateOptions: this.dateRange,
        warehouseArr:[],
        warehouse:'',
        type:null,
        typeIdArr:[],
        name:'',
        treeOptions:[],
        normalizer(node) {
          return {
            id: node.id,
            label: node.name,
            children: node.children,
          }
        },
        //入库详情
        goodsName:'',
        inventoryModal:false,
        detailCol:[
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '校区',
            align: 'center',
            key: 'name',
          },
          {
            title: '现存量',
            align: 'center',
            key: 'inventory',
          }
        ],
        detailData:[],
        saveLoading:false
      }
    },
    created(){
      this.getChargedStoreRoom().then((response)=>{
        this.warehouseArr = response.data;
      });
      this.inventoryStatistics(this.warehouse,this.typeIdArr.join(','),this.name);
      this.getKind();
    },
    methods: {
      //物品种类
      onSelect(value,instanceId){
        this.typeIdArr = value;
      },
      //获取分类
      getKind(){
        this.treeOptions= [];
        this.getGoodsKindTree().then((res)=>{
          this.treeOptions = res.data;
        })
      },
      //入库详情
      showDetail(index){
        this.inventoryModal = true;
        this.detailData = this.data[index].storeDetail;
        this.goodsName = this.data[index].name;
      },
      //列表
      inventoryStatistics(warehouseIds,goodsKindIds,goodsName) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!inventoryStatistics.action',
          data:{
            warehouseIds:warehouseIds,
            goodsKindIds:goodsKindIds,
            goodsName:goodsName,
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.length>0){
              aData.map((item)=>{
                this.data.push({
                  id:item.id,
                  name:item.name,
                  code:item.code,
                  classify:item.goodsKindName,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  standingStock:item.inventory,
                  storeDetail:item.storeDetail,
                  amount:item.price,
                })
              })
            }
            this.total = aData.totalRecord;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //查询
      selectHandle(){
        this.selectLoading = true;
        this.inventoryStatistics(this.warehouse,this.typeIdArr.join(','),this.name);
      },
      //导出
      exportHandle(){
        location.href = this.serverPath + "/sr/storeroomData!exportInventoryStatistics.action?warehouseIds="+this.warehouse+"&goodsKindIds="+this.typeIdArr.join(',')+"&goodsName="+this.name;
      }
    },
    mounted() {

    }
  }
</script>
<style lang="less" scoped>


</style>



